:root {
  --primary-color: #fff45e;
  --secondary-color:  #fff;
  --tertiary-color: #1f1f1f;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  font-family: sans-serif;
  background-color: var(--secondary-color);
  margin: 0;
  padding: 0;
}

main {
  width: 90%;
  margin: 32px auto;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 50%;
  font-style: italic;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}
 
.skip-link:focus {
   top: 0;
}

.restaurant-main-title {
  text-align: center;
  margin: 40px 0;
  font-weight: lighter;
}

app-bar {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  position: absolute;
}

hero-bar{
  height: 50vh;
}

img {
  display: block;
}

.restaurant-container{
  display: grid;
  gap: 1em;
  margin: 2em auto;
}

/* Restaurant */
.restaurant-item {
  display: block;
  margin-bottom: 18px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  overflow: hidden;
  line-height: 2em;
}

.restaurant-img {
  width: 100%;
  max-height: 300px;
  object-fit: cover;
  object-position: center;
}


.restaurant-info {
  padding: 32px;

}


.restaurant-info > .name{
  font-size: 44px;
  color: var(--secondary-color);
}

.restaurant-info.name:hover{
  color: var(--primary-color);
  transition: 0.3s ;
}

.rating {
  display: flex;
  align-items: center;
  font-size: 30px;
}
.rating p{
  margin-left: 10px;
}

.restaurant-info a {
  text-decoration: none;
  color: black;
}

.restaurant-info .city {
  font-size: 20px;
  color: grey;

}

.restaurant-info .description {

  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 10; /* number of lines to show */
  font-size: 20px;
}

.support_data {
  margin-top: 1em;
}

/* Review */
.review {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 20px;
}
.review-info {
  padding: 32px;  
}

.review_list {
  display: flex;
  flex-flow: column;

}

.review_user_container{
  width: 200px;
  height: 200px;
  box-shadow: 2px 2px 5px 5px rgba(0, 0, 0, 0.2);
  border-radius: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.review_user {
  width: 60%;
  height: 60%;

}

.review_name {
  margin-top: 0.5em;
}




/* Like Button */
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}

/* Footer */
footer {
  margin-top: 4em;
  min-height: 30vh;
  height: min-content;
  background-color: var(--tertiary-color);
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}

footer p {
  color: var(--secondary-color);
  margin-bottom: 2em;
}

.socmed {
  display: flex;
  align-items: center;
}

.socmed a{
  margin: 10px 20px;
  width: 2em;
  height: 2em;
  width: 50px;
  height: 50px;
}


.instagram {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: 100%;
  background-repeat: no-repeat;
}
.instagram:hover{
  background-size: 80%;
  transition: .3s;
}

.facebook {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: 100%;
  background-repeat: no-repeat;
}
.facebook:hover{
  background-size: 80%;
  transition: .3s;
}

.linkedin {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-size: 100%;
  background-repeat: no-repeat;
}
.linkedin:hover{
  background-size: 80%;
  transition: .3s;
}

/* Subscribe Button */
.btn {
  display: inline-block;
  padding: 12px 24px;
  margin: 20px 20px;
  background-color: var(--primary-color);
  border: 1px solid #770000;
  border-radius: 4px;
 
  font-weight: 500;
  font-size: 16px;
  color: var(--tertiary-color);
 
  cursor: pointer;
  transition: .15s ease-in-out;
}
 
.btn:hover {
  background-color: #DB0000BB;
}
.btn:active {
  background-color: #DB0000DD;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAGA,iCAAiC;AACjC;EACE,wBAAwB;EACxB,wBAAwB;EACxB,yBAAyB;AAC3B;;AAEA;EACE,UAAU;EACV,SAAS;EACT,sBAAsB;EACtB,kCAAkC;AACpC;;AAEA;EACE,uBAAuB;EACvB,wCAAwC;EACxC,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,UAAU;EACV,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,SAAS;EACT,kBAAkB;EAClB,yBAAyB;EACzB,YAAY;EACZ,YAAY;EACZ,YAAY;AACd;;AAEA;GACG,MAAM;AACT;;AAEA;EACE,kBAAkB;EAClB,cAAc;EACd,oBAAoB;AACtB;;AAEA;EACE,aAAa;EACb,qCAAqC;EACrC,kBAAkB;AACpB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,QAAQ;EACR,gBAAgB;AAClB;;AAEA,eAAe;AACf;EACE,cAAc;EACd,mBAAmB;EACnB,0CAA0C;EAC1C,mBAAmB;EACnB,gBAAgB;EAChB,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,iBAAiB;EACjB,iBAAiB;EACjB,uBAAuB;AACzB;;;AAGA;EACE,aAAa;;AAEf;;;AAGA;EACE,eAAe;EACf,6BAA6B;AAC/B;;AAEA;EACE,2BAA2B;EAC3B,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,eAAe;AACjB;AACA;EACE,iBAAiB;AACnB;;AAEA;EACE,qBAAqB;EACrB,YAAY;AACd;;AAEA;EACE,eAAe;EACf,WAAW;;AAEb;;AAEA;;EAEE,gBAAgB;EAChB,uBAAuB;EACvB,oBAAoB;EACpB,4BAA4B;EAC5B,sBAAsB,EAAE,4BAA4B;EACpD,eAAe;AACjB;;AAEA;EACE,eAAe;AACjB;;AAEA,WAAW;AACX;EACE,aAAa;EACb,iBAAiB;EACjB,uBAAuB;EACvB,mBAAmB;EACnB,kBAAkB;EAClB,aAAa;AACf;AACA;EACE,aAAa;AACf;;AAEA;EACE,aAAa;EACb,iBAAiB;;AAEnB;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,8CAA8C;EAC9C,mBAAmB;EACnB,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;AACA;EACE,UAAU;EACV,WAAW;;AAEb;;AAEA;EACE,iBAAiB;AACnB;;;;;AAKA,gBAAgB;AAChB;EACE,WAAW;EACX,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,yBAAyB;;EAEzB,eAAe;EACf,YAAY;EACZ,WAAW;EACX,kBAAkB;EAClB,SAAS;;EAET,eAAe;EACf,YAAY;EACZ,eAAe;AACjB;;AAEA,WAAW;AACX;EACE,eAAe;EACf,gBAAgB;EAChB,mBAAmB;EACnB,uCAAuC;EACvC,aAAa;EACb,iBAAiB;EACjB,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,6BAA6B;EAC7B,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;EACjB,UAAU;EACV,WAAW;EACX,WAAW;EACX,YAAY;AACd;;;AAGA;EACE,mDAAkE;EAClE,qBAAqB;EACrB,4BAA4B;AAC9B;AACA;EACE,oBAAoB;EACpB,eAAe;AACjB;;AAEA;EACE,mDAAiE;EACjE,qBAAqB;EACrB,4BAA4B;AAC9B;AACA;EACE,oBAAoB;EACpB,eA